"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import useUser from "@/hooks/useUser";
import { useMqttClient } from "@/hooks/useMqttClient";
import { getKitchenReadyOrderTopic, getCustomerOrderUpdateTopic } from "@/utils/mqttTopic";
import {
    editOrderStatus,
    getOrderById,
    getReadyOrders,
} from "@/app/actions/order";

export default function ReadyOrdersPage() {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(true);
    const { user, loading: userLoading } = useUser();
    const [topic, setTopic] = useState("");

    const { messages, publishMessage } = useMqttClient({
        subscribeTopics: topic ? [topic] : [],
    });

    useEffect(() => {
        if (userLoading) {
            return;
        }
        setTopic(getKitchenReadyOrderTopic("#"));

        const getOrders = async () => {
            try {
                // action
                let data = await getReadyOrders();
                if (!data) {
                    // api
                    const response = await fetch(`/api/order/ready`);
                    if (!response.ok) {
                        alert("Áç≤ÂèñÂÆåÊàêË®ÇÂñÆÂ§±Êïó");
                        return;
                    }
                    data = await response.json();
                }
                setOrders(data);
                setLoading(false);
            } catch (err) {
                alert("Áç≤ÂèñÂÆåÊàêË®ÇÂñÆÂ§±Êïó");
            }
        };
        getOrders();
    }, [userLoading]);

    // Áï∂Êé•Êî∂Âà∞ MQTT Ë®äÊÅØÊôÇÔºåÊõ¥Êñ∞Ë®ÇÂñÆÂàóË°®
    useEffect(() => {
        if (messages.length === 0) return;

        const lastMessage = messages[messages.length - 1];

        try {
            const newOrder = JSON.parse(lastMessage.payload);

            setOrders((prev) => {
                // Ê™¢Êü•ÊòØÂê¶Â∑≤Â≠òÂú®Áõ∏Âêå ID ÁöÑË®ÇÂñÆ
                const exists = prev.some((order) => order.id === newOrder.id);
                return exists
                    ? prev
                    : [
                          ...prev,
                          {
                              ...newOrder,
                              id: newOrder.orderId || newOrder.id,
                          },
                      ];
            });
        } catch (err) {
            console.error("ÁÑ°Ê≥ïËß£Êûê MQTT Ë®äÊÅØ:", err);
        }
    }, [messages]);

    const handleCompleteButton = async (orderId) => {
        let data = await editOrderStatus({ status: "COMPLETED" }, orderId);
        if (!data) {
            const response = await fetch(`/api/order/${orderId}/status`, {
                method: "PATCH",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ status: "COMPLETED" }),
            });
            if (!response.ok) {
                alert("‰øÆÊîπË®ÇÂñÆÁãÄÊÖãÂ§±Êïó");
                return;
            }
            data = await response.json(); // Âä†ÈÄôË°å
        }

        // ÂÖàÂæûÁï´Èù¢ÁßªÈô§
        setOrders((prev) => prev.filter((o) => o.id !== orderId));

        // ÂèñÂæóÂÆåÊï¥Ë®ÇÂñÆË≥áÊñôÔºàÁî®ÊñºÈÄöÁü•È°ßÂÆ¢Ôºâ
        let orderData = await getOrderById(orderId);
        if (!orderData) {
            const orderRes = await fetch(`/api/order/${orderId}`);
            if (!orderRes.ok) {
                alert("Áç≤ÂèñË®ÇÂñÆË©≥ÊÉÖÂ§±Êïó");
                return;
            }
            orderData = await orderRes.json();
        }

        // ÁôºÂ∏É MQTT ÈÄöÁü•Áµ¶È°ßÂÆ¢
        const customerId = orderData.customer?.id;
        if (!customerId) {
            console.warn("Êâæ‰∏çÂà∞È°ßÂÆ¢ IDÔºåÁÑ°Ê≥ïÁôºÂ∏É MQTT Ë®äÊÅØ");
            return;
        }

        const topic = getCustomerOrderUpdateTopic(customerId); // È°ßÂÆ¢Â∞àÂ±¨Êõ¥Êñ∞‰∏ªÈ°å
        const payload = JSON.stringify({
            type: "ORDER_COMPLETED",
            orderId: orderData.id,
            status: "COMPLETED",
            timestamp: new Date().toISOString(),
        });

        publishMessage(topic, payload);
        console.log("üì¢ Â∑≤ÁôºÂ∏É MQTT ÈÄöÁü•Ôºö", topic, payload);
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-yellow-50 via-pink-50 to-red-50 py-10 px-6">
            <div className="max-w-5xl mx-auto">
                <h1 className="text-3xl font-bold text-gray-800 mb-8">
                    üç± ÂÆåÊàêÁöÑË®ÇÂñÆ
                </h1>

                {loading ? (
                    <div className="space-y-4">
                        {[...Array(3)].map((_, i) => (
                            <div
                                key={i}
                                className="animate-pulse h-24 bg-white rounded-lg shadow"
                            />
                        ))}
                    </div>
                ) : orders.length === 0 ? (
                    <div className="text-center text-gray-500 mt-20 text-lg">
                        üéâ ÁõÆÂâçÊ≤íÊúâÂÆåÊàêÁöÑË®ÇÂñÆ
                    </div>
                ) : (
                    <motion.div
                        layout
                        className="grid grid-cols-1 sm:grid-cols-2 gap-6"
                    >
                        {orders.map((order) => (
                            <motion.div
                                key={order.id}
                                layout
                                className="bg-white rounded-xl shadow-md p-5 hover:shadow-xl transition-shadow duration-300"
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.3 }}
                            >
                                <h2 className="text-xl font-bold text-pink-600 mb-2">
                                    Ë®ÇÂñÆ #{order.id.slice(0, 8)}
                                </h2>
                                <p className="text-gray-800 font-medium mb-1">
                                    È°ßÂÆ¢Ôºö{order.customer?.name}
                                </p>
                                <ul className="text-sm list-disc pl-5 mb-2 space-y-1">
                                    {order.items.map((item, idx) => (
                                        <li key={idx}>
                                            {item.menuItem.name}

                                            <p className="text-xs text-gray-500 ml-4">
                                                ÂÇôË®ªÔºö{item.specialRequest}
                                            </p>
                                        </li>
                                    ))}
                                </ul>

                                <p className="text-xs text-gray-500">
                                    Ë®ÇÂñÆÂª∫Á´ãÊôÇÈñìÔºö{order.createdAt}
                                </p>
                                <button
                                    className="mt-4 w-full bg-green-500 hover:bg-green-600 text-white py-2 rounded-md font-semibold transition"
                                    onClick={() => {
                                        handleCompleteButton(
                                            order.orderId || order.id
                                        );
                                    }}
                                >
                                    ‚úÖ Â∑≤‰∫§‰ªò
                                </button>
                            </motion.div>
                        ))}
                    </motion.div>
                )}
            </div>
        </div>
    );
}
